body {
  font-family: "Arial", sans-serif;
  
  background: linear-gradient(135deg, #c8dd07, #e608ae);
  color: #333;
  text-align: center;
  padding: 50px;
  transition: all 0.3s ease-in-out;
}

h1 {
  display: flex;
   background: linear-gradient(rgb(206, 8, 212),rgb(231, 10, 113));
   border-radius: 50px;
   width: 450px;
  font-size: 36px;
  color: #fff;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.flex-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-top: 30px;
}

.left-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.right-section {
  flex: 2;
}

.round-btn {
  width: 70px;
  height: 70px;
  font-size: 18px;
  border-radius: 50%;
  border: none;
  background: linear-gradient(135deg, #f8ffae, #43c6ac);
  color: #333;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.round-btn:hover {
  transform: scale(1.1);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.stop-btn {
  width: 70px;
  height: 70px;
  font-size: 18px;
  border-radius: 50%;
  border: none;
  background: #ff4d4d;
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease-in-out;
}

.stop-btn:hover {
  transform: scale(1.1);
}

.timer {
  font-size: 20px;
  margin-top: 10px;
  color: #fff;
  background: rgba(0, 0, 0, 0.3);
  padding: 10px;
  border-radius: 8px;
  display: inline-block;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
  margin-top: 20px;
  padding: 20px;
}

.grid-item {
  padding: 15px;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  backdrop-filter: blur(8px);
  transition: transform 0.3s ease-in-out;
}

.grid-item:hover {
  transform: scale(1.05);
}

.mic-icon {
  font-size: 24px;
}

.divider {
  width: 2px; /* Thin vertical line */
  background: rgba(0, 0, 0, 0.3);
  margin: 0 20px;
}